import { m } from "framer-motion";
import React, { useState } from "react";
import ReactPlayer from "react-player/lazy";
import styled from "styled-components";
import { config } from "../../config/api";
import FormStyleProvider from "./FormStyleProvider";
import NumberItem from "./NumberItem";

const Form2 = ({ numbers, className }) => {
  const [url, seturl] = useState(
    numbers?.[0]?.video && `${config.apiResource}${numbers?.[0]?.video}`
  );
  const handleVideo = (url) => {
    url && seturl(`${config.apiResource}${url}`);
  };
  return (
    <FormStyleProvider>
      <div
        className={`step__form--general page--2 d-flex flex-column flex-lg-row ${className}`}
      >
        <div className="step__form--general--left">
          <p className="fs__title--main fw-bolder">Bài báo cáo</p>
          <m.div layout className="step__form__video">
            {url && (
              <ReactPlayer
                width="100%"
                height="auto"
                url={url}
                controls={true}
                muted={true}
                playing={true}
                style={{ width: "100%", height: "100%" }}
              />
            )}
          </m.div>
          <m.div layout>
            {numbers?.map((video, i) => {
              const { name_i, number_order } = video || {};
              return (
                <m.div
                  layout
                  className="card"
                  key={i}
                  onClick={() => handleVideo(video?.video)}
                >
                  <div className="card__video">
                    <img
                      src="https://www.sohoceravietnam.com/public/img/314a50ada9025a5c0313.jpg"
                      alt=""
                    />
                  </div>
                  <div className="card__body">
                    <p className="card__body__title fs__text fw-bolder">
                      {name_i} {number_order}
                    </p>
                    <p className="card__body__date fs__smallest">
                      {video?.desc_i}
                    </p>
                  </div>
                </m.div>
              );
            })}
          </m.div>
          {/* {url && (
            <div className="step__form__video">
              <ReactPlayer
                width="100%"
                height="auto"
                url={url}
                controls={true}
                muted={true}
                playing={true}
                style={{ width: "100%", height: "100%" }}
              />
            </div>
          )}
          <div>
            {numbers?.map((number, i) => (
              <NumberItem
                key={i}
                name={number?.name_i}
                value={i > 1 ? null : number?.number_order}
                onClick={i > 1 ? null : () => handleVideo(number?.video)}
              />
              // <p
              //   onClick={() => handleVideo(number?.video)}
              //   className="d-flex justify-content-between"
              // >
              //   Chỉ số {number?.name_i} <span>{number?.number_order}</span>
              // </p>
            ))}
          </div> */}
        </div>
      </div>
    </FormStyleProvider>
  );
};

export default styled(Form2)`
  .step__form--general--left {
    width: 100% !important;
    .card {
      cursor: pointer;
      margin-top: 20px;
      display: flex;
      flex-direction: row;
      background-color: ${(props) => props.theme.colorCard};
      border-radius: 16px;
      padding: 10px 20px 10px 10px;
      &__video {
        width: 150px;
        height: 100px;
        margin-right: 20px;
        img {
          border-radius: 16px;
          width: auto;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    .user__management {
      &__body {
        display: flex;
        flex-direction: column;
        p:first-child {
          margin-bottom: 5px;
        }
      }
    }
    &__video {
      /* width: 60%; */
      .card {
        cursor: pointer;
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        background-color: ${(props) => props.theme.colorCard};
        border-radius: 16px;
        padding: 10px 20px 10px 10px;
        &__video {
          width: 150px;
          height: 100px;
          margin-right: 20px;
          img {
            border-radius: 16px;
            width: auto;
            height: 100%;
            object-fit: cover;
          }
        }
        &__body {
          display: flex;
          flex-direction: column;
          p:first-child {
            margin-bottom: 5px;
          }
        }
      }
    }
  }
`;
